import Plus from 'vue-material-design-icons/Plus.vue';
import FolderMultipleImage from 'vue-material-design-icons/FolderMultipleImage.vue';
import { generateUrl } from '@nextcloud/router';
import { NcModal, NcButton, NcEmptyContent } from '@nextcloud/vue';
import { translate, translatePlural } from '@nextcloud/l10n';
import { getCurrentUser } from '@nextcloud/auth';
import CollectionsList from '../components/Collection/CollectionsList.vue';
import CollectionCover from '../components/Collection/CollectionCover.vue';
import HeaderNavigation from '../components/HeaderNavigation.vue';
import AlbumForm from '../components/Albums/AlbumForm.vue';
import FetchCollectionsMixin from '../mixins/FetchCollectionsMixin.js';
export default {
  name: 'Albums',
  components: {
    Plus,
    FolderMultipleImage,
    NcModal,
    NcButton,
    NcEmptyContent,
    CollectionsList,
    CollectionCover,
    HeaderNavigation,
    AlbumForm
  },
  filters: {
    /**
     * @param {string} lastPhoto The album's last photos.
     */
    coverUrl(lastPhoto) {
      if (lastPhoto === -1) {
        return '';
      }
      return generateUrl(`/apps/photos/api/v1/preview/${lastPhoto}?x=${512}&y=${512}`);
    }
  },
  mixins: [FetchCollectionsMixin],
  data() {
    return {
      showAlbumCreationForm: false
    };
  },
  computed: {
    /**
     * @return {import('../services/Albums').IndexedAlbums}
     */
    albums() {
      return this.$store.getters.albums;
    }
  },
  async beforeMount() {
    this.fetchAlbums();
  },
  methods: {
    fetchAlbums() {
      this.fetchCollections(`/photos/${getCurrentUser()?.uid}/albums`, ['<nc:location />', '<nc:dateRange />', '<nc:collaborators />']);
    },
    handleAlbumCreated(_ref) {
      let {
        album
      } = _ref;
      this.showAlbumCreationForm = false;
      this.$router.push(`albums/${album.basename}`);
    },
    t: translate,
    n: translatePlural
  }
};